/* Global reset for margins and padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body styling */
body {
  background-color: #f4f4f4;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Ensure the height adjusts dynamically */
  text-align: center;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* App container */
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 900px;
}

/* Styling for headings */
h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px;
}

h2 {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 10px;
}

h3 {
  font-size: 1.25rem;
  color: #666;
  margin-bottom: 15px;
}

/* Value selection buttons */
.value-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.value-button {
  padding: 10px 20px;
  background-color: #fff;
  border: 2px solid #007bff;
  border-radius: 5px;
  color: #007bff;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  width: 200px;
}

.value-button:hover {
  background-color: #007bff;
  color: #fff;
}

.value-button.selected {
  background-color: #28a745;
  color: white;
}

/* Scoring section */
.scoring-section {
  margin-bottom: 20px;
}

.score-bar {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.score-box {
  width: 30px;
  height: 30px;
  background-color: #ddd;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.score-box.selected {
  background-color: #28a745;
}

.submit-button {
  padding: 10px 20px;
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 5px;
  width: 200px;
  margin-top: 20px;
  transition: all 0.3s;
}

.submit-button:hover {
  background-color: #0056b3;
}

/* Make sure the content is aligned and centered on the page */
.centered-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* Style for the chart */
.chart-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  cursor: pointer;
}

.selection-buttons {
  margin-bottom: 20px;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 10px;
  justify-content: center; /* Ensures buttons stay aligned in the center */
}

.button-container button  {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #f0f0f0;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: calc(50% - 10px); /* Dynamic sizing for 2-column layout */
  cursor: pointer;
  transition: background-color 0.3s, border-color 0.3s;
}

.button-container button:selected {
  background-color: #4CAF50;
  color: white;
  border-color: #388E3C;
}

.button-container button:hover {
  background-color: #ddd;
  border-color: #bbb;
}

.submit-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-button:hover {
  background-color: #388E3C;
}
/* For screens wider than 768px (e.g., tablets, desktops) */
@media (min-width: 768px) {
  .App {
    padding: 20px;
    text-align: center;
  }

  h1 {
    font-size: 2.5rem;
  }

  .button-container {
    display: flex; /* Flex container for button layout */
    justify-content: space-evenly; /* Space buttons evenly */
    gap: 15px;
  }

  .button-container button {
    padding: 10px 20px;
    font-size: 1.2rem;
  }
}

/* For screens between 480px and 768px (e.g., tablets) */
@media (max-width: 768px) and (min-width: 480px) {
  .App {
    padding: 15px;
    text-align: center;
  }

  h1 {
    font-size: 2rem;
  }

  .button-container {
    display: flex; /* Flex container for button layout */
    flex-wrap: wrap; /* Allow buttons to wrap onto new lines */
    justify-content: space-evenly; /* Space buttons evenly */
    gap: 10px;
  }

  .button-container button {
    padding: 8px 15px; /* Smaller padding for tablet screens */
    font-size: 1.1rem; /* Slightly smaller font size for tablets */
    width: 45%; /* Make buttons smaller and fit 2 per row */
  }
}

/* For screens smaller than 480px (e.g., smartphones) */
@media (max-width: 480px) {
  .App {
    padding: 10px;
    text-align: center;
  }

  h1 {
    font-size: 1.5rem; /* More compact for small screens */
  }

  .button-container {
    gap: 5px; /* Reduce gaps */
  }

  .button-container button {
    padding: 8px 10px; /* Compact padding for buttons */
    font-size: 0.9rem; /* Smaller font size for small screens */
    width: calc(45% - 5px); /* Dynamically adjust to fit 2 per row */
  }
}

